body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(0, 245, 0), rgb(2, 92, 30));
  /* background: linear-gradient(to right, rgb(46, 49, 46), rgb(128, 152, 136)) */
  /* background: linear-gradient(to right, rgb(151, 151, 151), rgb(247, 247, 247)) */
  background: linear-gradient(to right, rgb(29, 29, 29), rgb(15, 13, 13))
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
